import { useStaticQuery, graphql } from 'gatsby';
import { FluidObject } from 'gatsby-image';

export type PostsProps = {
  totalCount: number;
  edges: Array<{
    node: {
      id: string;
      excerpt: string;
      fields: {
        slug: string;
      };
      frontmatter: {
        title: string;
        subTitle?: string;
        date?: string;
        category?: string;
        cover?: {
          childImageSharp: {
            fluid: FluidObject;
          };
        };
      };
    };
  }>;
};

export type PagesProps = {
  edges: Array<{
    node: {
      fields: {
        slug: string;
      };
      frontmatter: {
        title: string;
      };
    };
  }>;
};

type LayoutQueryType = {
  data: {
    posts: PostsProps;
    pages: PagesProps;
  };
};

export const useLayoutQuery = (): LayoutQueryType => {
  const layoutData = useStaticQuery(
    graphql`
      query LayoutData {
        posts: allMdx(filter: { fileAbsolutePath: { regex: "//posts//" } }) {
          totalCount
          edges {
            node {
              id
              excerpt
              fields {
                slug
              }
              frontmatter {
                title
                subTitle
                date(formatString: "YYYY.MM.D")
                category
                cover {
                  publicURL
                  childImageSharp {
                    fluid(quality: 100, srcSetBreakpoints: [30, 60, 80, 200]) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
        pages: allMdx(filter: { fileAbsolutePath: { regex: "//pages//" } }) {
          edges {
            node {
              fields {
                slug
              }
              frontmatter {
                title
              }
            }
          }
        }
      }
    `,
  );
  return layoutData;
};
